import { useMutation, useQuery } from '@tanstack/react-query';
import React, { useRef } from 'react';
import { useRecoilValue } from 'recoil';
import {
  cancleInvitation,
  getInvitationList,
  resendInvitation,
} from '../../../../api/Invite/inviteApi';
import { Grid } from '../../../../components/common/Grid/Grid';
import Description from '../../../../components/UI/Atoms/Ex/Description';
import Em from '../../../../components/UI/Atoms/Ex/Em';
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom';
import PageContainer from '../../../../components/layout/PageContainer';
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage';
import WhiteBtnSM from '../../../../components/UI/Atoms/Button/WhiteBtnMD';
import BlackBtnSM from '../../../../components/UI/Atoms/Button/BlackBtnSM';
import { toAge } from '../../../../utils/render/toAge';
import { getRoleText } from '../../../../utils/render/getRoleText';
import { useModal } from '../../../../hooks/basic/useModal';
import ModalContainerAlert from '../../../../components/layout/ModalContainerAlert';
import AlertText from '../../../../components/common/Alert/AlertText';
import RankImage from '../../../../components/common/Rank/RankImage';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { phoneHyphenRegex } from '../../../../utils/lib/Regex';
import moment from 'moment';
import { getFullname } from '../../../../utils/render/getFullname';

const InviteManagePage = () => {
  const dojangId = useRecoilValue(dojangIdAtom);

  // custom hook
  const [openCancle, openCancleFn, closeCancleFn] = useModal();

  // query
  const { data } = useQuery(['getInvitationList', dojangId], () => getInvitationList(dojangId), {
    enabled: !!dojangId,
    select: (res) => {
      let temp = res?.map((item) => {
        let dd = new Date(item.invite_info.expire_date);
        return {
          ...item,
          invite_info: {
            ...item.invite_info,
            expire_date: moment(dd).format('YYYY-MM-DD HH:mm'),
          },
        };
      });

      return temp;
    },
  });

  const { mutate: CancleMt, isLoading: cancleLd } = useMutation((inviteId) =>
    cancleInvitation(inviteId),
  );

  const inviteId = useRef('');

  // handler
  const handleClickCancel = (e) => {
    const { id } = e.target;
    inviteId.current = id;
    openCancleFn();
  };

  const handleCancle = (e) => {
    CancleMt(inviteId.current, {
      onSuccess: () => window.location.reload(),
      onError: () => {
        alert('오류가 발생하였습니다. 잠시후 다시 시도해 주세요.');
        window.location.reload();
      },
    });
  };

  // useDebug(data);

  return (
    <PageContainer>
      <Grid>
        <Grid.Head>
          <Description>
            현재 초대중인 회원은 <Em>{`${data?.length || '0'}`}명</Em> 입니다.
          </Description>
        </Grid.Head>
        <Grid.RowWrap>
          {data &&
            React.Children.toArray(
              data?.map((item) => (
                <Grid.Row className="text-[#333333]">
                  {item?.invite_info?.student && (
                    <>
                      <Grid.Col fixed="60px" className="pr-0">
                        <ProfileImage urlKey={item?.student_info?.photo_url} />
                      </Grid.Col>
                      <Grid.Col fixed="200px">
                        <div className="flex flex-col gap-[2px]">
                          <span className="text-[13px] text-[#5E81F4]">수련생</span>
                          <span className="flex">
                            <span className="font-bold w-[88px]">
                              {getFullname(
                                item?.student_info?.last_name,
                                item?.student_info?.first_name,
                              ) || '이름없음'}
                            </span>
                            <span className="w-[60px]">
                              {toAge(item?.student_info?.dob) || ' '}{' '}
                            </span>
                            <RankImage src={item?.student_info?.level} />
                          </span>
                        </div>
                      </Grid.Col>
                      <Grid.Col fixed="140px">
                        <div className="flex flex-col gap-[2px]">
                          <span className="text-[13px] text-[#5E81F4]">관계</span>
                          <span>
                            {item?.invite_info?.recipient_type === 'FAMILY'
                              ? '가족'
                              : item?.invite_info?.recipient_type}
                          </span>
                        </div>
                      </Grid.Col>
                    </>
                  )}
                  {!item?.invite_info?.student && (
                    <>
                      <Grid.Col fixed="60px" className="pr-0">
                        <ProfileImage urlKey={item?.photo_url} />
                      </Grid.Col>
                      <Grid.Col fixed="240px">
                        {getFullname(item?.user_info?.last_name, item?.user_info?.first_name) ||
                          '이름없음'}
                        <span className="ml-1">
                          {getRoleText(item?.invite_info?.recipient_type)}
                        </span>
                      </Grid.Col>
                      <Grid.Col fixed="100px">　</Grid.Col>
                    </>
                  )}

                  <Grid.Col fixed="200px">
                    <div className="flex flex-col gap-[2px]">
                      <span className="text-[13px] text-[#5E81F4]">연락처</span>
                      <span>{phoneHyphenRegex(item?.invite_info?.phone_number)}</span>
                    </div>
                  </Grid.Col>
                  <Grid.Col fixed="200px">
                    <div className="flex flex-col gap-[2px]">
                      <span className="text-[13px] text-[#5E81F4]">초대장 만료일</span>
                      <span className="text-[14px] tracking-wide">
                        {item?.invite_info?.expire_date}
                      </span>
                    </div>
                  </Grid.Col>
                  <Grid.Col fixed="140px">
                    <div className="flex flex-col text-center gap-[2px]">
                      <div className="flex m-auto">
                        <WhiteBtnSM
                          id={item?.invite_info?.id}
                          onClick={handleClickCancel}
                          className="mr-[2px]"
                        >
                          삭제
                        </WhiteBtnSM>
                      </div>
                    </div>
                  </Grid.Col>
                </Grid.Row>
              )),
            )}
        </Grid.RowWrap>
      </Grid>

      <ModalContainerAlert
        open={openCancle}
        onClose={closeCancleFn}
        confirmText="삭제"
        onConfirm={handleCancle}
        isLoading={cancleLd}
      >
        <AlertText title>초대장을 삭제합니다.</AlertText>
        <AlertText>
          보낸 초대장이 삭제됩니다. <br />
          삭제한 초대장은 다시 확인할 수 없습니다.
        </AlertText>
      </ModalContainerAlert>
    </PageContainer>
  );
};

export default InviteManagePage;
